// ----------------------------------------------------------------
// topページ専用
// ----------------------------------------------------------------
@import "_setting/_variables";
@import "_setting/_mixin";
$diagnosis-bg: #FFFFE3;

.top-main{
    .container{
            position: relative;      
    }
}
.top-flow {
    .container {
        @include desktop {
            max-width: 1024px;
        }
    }
}
.top-info,
.top-faq {
    .container {
        @include desktop {
            max-width: 700px;
        }
    }
}
// main
// -----------------------------------
.top-main {
    background: url(../images/top-main-bg.jpg) no-repeat center center;
    background-size: cover;
    text-align: center;
    color: #ffffff;
    font-weight: bold;
    font-size: 20px;
    @include desktop {
        height: 640px;
        padding-top: 90px;
    }
    @include touch {
        background-image: url(../images/top-main-bg-sp.jpg);
        background-size: contain;
        background-position: top center; 
        padding-top: 46px;
        padding-bottom: 80px;
        height: 580px;
    }
    &-hd-01 {
        position: absolute;
        @include touch {
            width: 298px;
            left: 50%;
            transform: translateX(-50%);
        }
        img {
            @include desktop {
                width: 380px;
            }
            @include touch {
                width: 298px;
            }
        }
        margin-bottom: 25px;
        @include touch {
            margin-bottom: 10px;
        }
    }
    &-hd-02 {
        position: absolute;
        font-weight: bold;
        background: url(../images/top-main-sticker.png) no-repeat center center;
        background-size: 353px 354px;
        width: 353px;
        height: 354px;
        padding-top: 210px;
        @include desktop {
            top: 150px;
            left: 20px;
        }
        @include touch {
            font-size: 16px;
            top: 320px;
            left: 50%;
            transform: translateX(-50%);
        }
    }
    &-device {
        position: absolute;
        @include desktop {
            width: 637px;
            top: 160px;
            right: 0;
        }
        @include touch {
            width: 300px;
            top: 120px;
            left: 50%;
            transform: translateX(-50%);

        }
        &-text {
            color: #f5f349;
            letter-spacing: 0.83px;
            font-size: 16.5px;
            font-weight: bold;
            text-shadow: 1px 1px 6px #666666;
            @include touch {
                font-size: 12px;
            }
            &::before,
            &::after {
                content: "";
                display: inline-block;
                height: 16px;
                width: 2px;
                background-color: #f5f349;
                box-shadow: 1px 1px 6px #666666;
                margin: 0 8px;
                @include touch {
                    height: 14px;
                }
            }
            &::before {
                transform: rotate(-30deg);
                transform-origin: bottom;
            }
            &::after {
                transform: rotate(30deg);
                transform-origin: bottom;
            }
        }
    }
}

// service
// -----------------------------------
.top-service {
    text-align: center;
    .section{
        @include touch {
            padding-top: 180px;
        }
    }
    &-title {
        @include desktop {
        }
        @include touch {
        }
        &-inner {
            width: 300px;
            height: 300px;
            border-radius: 100%;
            box-shadow: 0px 3px 18px 0 rgba(0, 0, 0, 0.1);
            display: flex;
            justify-content: center;
            padding-top: 50px;
            @include touch {
                width: 226px;
                height: 226px;
                padding-top: 35px;
            }
        }
    }
    &-introduction {
        @include desktop {
            display: flex;
            align-content: flex-start;
            margin-top: 40px;
        }
        @include touch {
            margin-top: 20px;
        }
    }
    &-figure {
        @include desktop {
            flex-basis: 450px;
            margin-right: 40px;
        }
        @include touch {
            margin: 0 -10px;
        }
    }
    &-list {
        text-align: left;
        @include desktop {
            flex-basis: calc(100% - 490px);;
        }
        @include touch {
            margin: 30px auto;
        }
        &-item {
            @include desktop {
            }
            & + & {
                @include touch {
                }
            }
            &:not(:last-child){
                margin-bottom: 20px;
            }
        }
    }
    &::after {
        content: "";
        position: relative;
        bottom: -60px;
        margin: 0 auto;
        border: 60px solid transparent;

        @include touch{
            bottom: -20px; 
        }
    }
}

// graph
// -----------------------------------
.top-graph {
    text-align: center;
    &-figure{

        margin: 0 auto;
        @include desktop{
            width: 1004px;
            padding: 30px;
        }
        @include touch{
            width: 100%;
            padding: 30px 5px;
        }
    }
    &-button {
        text-align: center;
        font-weight: bold;
        ul{
            @include desktop {
                display: flex;
                justify-content: center;
                margin: 10px 0;
            }
            @include touch{
                margin: 20px 0 0;
            }
        }
        li{
            @include desktop {
                margin: 0 110px;
            }
            @include touch{
                &:not(:last-child){
                    margin-bottom: 20px;
                }
            }
        }
    }
    &::after {
        content: "";
        position: relative;
        bottom: -40px;
        margin: 0 auto;
        border: 60px solid transparent;

    }
}


// flow
// -----------------------------------
.top-flow {
    @include touch{
        .section{
            padding: 60px 10px;
        }
    }
    &-list{
        @include desktop {
            display: flex;
        }
        @include touch {
        }
        &-item{
            background-color: #ffffff;
            @include desktop{
                position: relative;
                padding: 30px 15px;
                width: 100%;
                &:not(:last-child){
                    margin-right: 6px;
                }
            }
            @include touch{
                display: flex;
                position: relative;
                padding: 20px 10px;
                &:not(:last-child){
                    margin-bottom: 10px;
                }
            }
            img{
                border-radius: 8px;
            }
            h3{
                @include desktop{
                    text-align: center;
                    margin: 20px 0;
                }
                @include touch{
                    text-align: left;
                }
            }
        }
        &-number{
            width: 40px;
            height: 40px;

            border-radius: 50%;
            text-align:center;
            line-height: 40px;
            color: $color-white;
            position: absolute;
            @include desktop{
                top: 10px;
                left: 5px;
            }
            @include touch{
                top: -5px;
                left: -5px;
            }
        }
        &-text{
            @include touch{
                flex-basis: calc(100% - 110px);
                text-align: left;
            }
        }
        &-figure{
            @include touch{
                flex-basis: 100px;
                margin-right: 10px;
            }
        }
        
    }
}

// info
// -----------------------------------
.top-info {
    @include touch{
        .section{
            padding: 60px 10px;
        }
    }
}

// faq
// -----------------------------------
.top-faq {
    @include touch{
        .section{
            padding: 60px 10px;
        }
    }
}